$gold: #dcaa30;


html,
body {
    height: 100%;
    padding-top: 25px;
    font-size: 1.1rem;
    line-height: 1.4rem;
    font-family: 'hp_simplifiedlight', sans-serif;
    font-weight: 200;
    color: #767676;
    background: #5A5A5A;
}

a {
	color: #0096d6;
	&:hover {
		color: #0171ad;
		text-decoration: none;
	}
}


.btn {
	text-transform: uppercase;
	font-weight: 300;
	background: #ffffff;
	border-color: #0171ad;
	color: #0096d6;
	border-radius: 3px;
	&:hover {
		background: #0096d6;
		border-color: #0096d6;
		color: #ffffff;
	}
}

.wrapper,
.row {
    height: 100%;
    margin-left: 0;
    margin-right: 0;
}

.wrapper:before,
.wrapper:after,
.content-column:before,
.content-column:after {
    content: "";
    display: table;
}

.wrapper:after,
.content-column:after {
    clear: both;
}

#sidebar {
    background-color: #5a5a5a;
    height: 100%;
    position: fixed;
    top: 0;
    right: 0;
    padding-top: 15px;
    padding-right: 20px;
    z-index: 999999;
}

.content-column {
	padding: 0;
	background: #ffffff;
}

.navbar {
	padding: 15px 15px 10px;
	&.navbar-default {
		background: #fcfcfc;
		border-color: #f6f6f6;
	}
	.navbar-brand {
		padding: 0;
		}
	}

